import { extend, useFrame } from "@react-three/fiber";
import { useRef } from "react";
// import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import CustomObject from "./CustomObject.js";
import { MeshReflectorMaterial, Float, Text, Html, PivotControls, TransformControls, OrbitControls } from "@react-three/drei";

// extend({ OrbitControls: OrbitControls })
export default function Experience() {
  const cubeRef = useRef();
  const groupRef = useRef();
  // const { camera, gl } = useThree()
  const sphere = useRef();
  const cube = useRef();
  useFrame((state, delta) => {
    // cubeRef.current.rotation.y += delta * 1
    // groupRef.current.rotation.y += delta
    const angle = state.clock.elapsedTime;
    // state.camera.position.x = Math.sin(angle) * 8
    // state.camera.position.z = Math.cos(angle) * 8
    // state.camera.lookAt(0, 0, 0)
  });
  return (
    <>
      {/* <OrbitControls enableDamping={ false }/> */}
      {/* <OrbitControls /> */}
      <OrbitControls makeDefault />

      <directionalLight position={[1, 2, 3]} intensity={1.5} />
      {/* <orbitControls args={ [ camera, gl.domElement ] } /> */}
      <ambientLight intensity={0.5} />

      <group ref={groupRef}>
        {/* <Html>Test</Html> */}
        <mesh position-x={-2}>
          <sphereGeometry />
          <meshStandardMaterial color="orange" />
          <Html distanceFactor={8} position={[1, 1, 0]} wrapperClass="label" center occlude={[sphere, cube]}>
           
          </Html>
        </mesh>

        {/* <CustomObject /> */}
        {/* <mesh ref={ cubeRef } rotation-y={Math.PI * 0.25} position-x={2} scale={1.5} > */}
        <PivotControls anchor={[0, 0, 0]} depthTest={true} lineWidth={4} axisColors={["#9381ff", "#ff4d6d", "#7ae582"]} scale={100} fixed={true} ref={sphere}>
          <mesh ref={cube} position-x={2} scale={1.5}>
            <boxGeometry />
            <meshStandardMaterial color="mediumpurple" />
          </mesh>
        </PivotControls>

        {/* <TransformControls object={cube} mode="translate" /> */}
      </group>
      <Float speed={5} floatIntensity={2}>
        <Text  font="./fonts/MSJH.TTC" fontSize={1} color="salmon" position-y={2}  textAlign="center">
         
        </Text>
      </Float>
      <mesh position-y={-1} rotation-x={-Math.PI * 0.5} scale={10}>
        <planeGeometry />
        <MeshReflectorMaterial  color="greenyellow" resolution={512} blur={[1000, 1000]} mixBlur={1} mirror={0.5} />
      </mesh>
    </>
  );
}
